import React, {useState, useRef} from 'react';

export default function Search({ updateResults }) {

  const [searchTerm, setSearchTerm] = useState('');
  const searchInput = useRef();

  function doSearch(e){
    e.preventDefault();
    // do some search request stuff
    console.log('searching...', searchTerm);
    // save the search term for future use?
    updateResults(searchTerm);
    setSearchTerm('');
    searchInput.current.focus();
  }

  return (
    <div id={'search'}>
      <form onSubmit={(e) => doSearch(e)}>
        <label htmlFor={'search-input'}>Search:</label>
        <>&nbsp;</>
        <input ref={searchInput} id={'search-input'} type={'text'} value={searchTerm} onChange={(e) => setSearchTerm(e.target.value)} />
        <>&nbsp;</>
        <button type={'submit'}>Search</button>
      </form>
    </div>
  )
}
