import React, {useState} from 'react';
import {Switch, Route, Redirect, NavLink} from 'react-router-dom';
import axios from 'axios';
import Search from './Search';
import History from './History';
import DisplaySearchResults from './DisplaySearchResults';

function App() {

  const [searchResults, setSearchResults] = useState([]);
  const [searchHistory, setSearchHistory] = useState([]);

  function updateResults(term) {

    axios.get(`http://hn.algolia.com/api/v1/search?query=${term}`).then((resp) => {
      console.log(resp);
      setSearchResults(resp.data.hits);
    });

    setSearchHistory([...searchHistory, {
      term,
      timestamp: Date.now()
    }]);

  }

  return (
    <div id={'app'}>
      <div id={'tabs'}>
        <h1><NavLink to={'/search'}>New Search</NavLink></h1>
        <h1><NavLink to={'/history'}>Search History</NavLink></h1>
      </div>
      <Switch>
        <Route path={'/search'}>
          <Search updateResults={updateResults}/>
          <DisplaySearchResults setSearchResults={setSearchResults} searchResults={searchResults}/>
        </Route>
        <Route path={'/history'}>
          <History setSearchHistory={setSearchHistory} searchHistory={searchHistory}/>
        </Route>
        <Route exact path={'/'}>
          <Redirect to={'/search'}/>
        </Route>
      </Switch>
    </div>
  );
}

export default App;
