import React from 'react';

export default function DisplaySearchResults({setSearchResults, searchResults = []}) {
  return (
    <div id={'display-search-results'}>
      <header>
        <h3>Search Results</h3>
        <button type={'button'} disabled={!searchResults.length} onClick={() => setSearchResults([])}>Clear</button>
      </header>
      <ul>
        {searchResults.map((item, i) => (
          <li data-object-id={item.objectID} key={item.objectID}>
            <a href={item.url} target={'_blank'} rel={'noreferrer'}>{item.title}</a>
          </li>
        ))}
      </ul>
    </div>
  )
}
