import React from 'react';

export default function History({setSearchHistory, searchHistory = []}) {
  return (
    <div id={'history'}>
      <header>
        <h3>Search History</h3>
        <button type={'button'} disabled={!searchHistory.length} onClick={() => setSearchHistory([])}>Clear</button>
      </header>
      {!!searchHistory.length && (
        <table style={{marginTop: 20, width: '100%'}}>
          <tbody>
          {searchHistory.map((item) => {
            const time = item.timestamp;
            return (
              <tr key={time}>
                <td className={'text-left'}>
                  {item.term}
                </td>
                <td className={'text-center font-mono'}>
                  {(new Date(time)).toLocaleString()}
                </td>
              </tr>
            )
          })}
          </tbody>
        </table>
      )}
    </div>
  )
}
